@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/VisueltPro-Bold.ttf);
}

@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/VisueltPro-Medium.ttf);
}

@font-face {
  font-family: "font-3";
  src: url(../assets/fonts/VisueltPro-Regular.ttf);
}

@font-face {
  font-family: "font-4";
  src: url(../assets/fonts/Helvetica.ttf);
}

@font-face {
  font-family: "font-5";
  src: url(../assets/fonts/VisueltPro-Thin.ttf);
}

@mixin texteffect {
  a,
  a:visited {
    position: relative;
    display: block;
    cursor: pointer;
    color: #515151;
    font-family: $font-3;
    text-decoration: underline;
    transition: 0.7s cubic-bezier(0.23, 1, 0.32, 1) 0s;

    &:hover {
      text-decoration: none;
    }
  }

  a:before,
  a:after {
    content: "";
    position: absolute;
    width: 0%;
    height: 2px;
    top: 50%;
    margin-top: -0.5px;
    background: #000;
  }

  a:before {
    left: -2.5px;
  }
  a:after {
    right: 2.5px;
    background: #000;
    transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
  }

  a:hover:before {
    background: #000;
    width: 110%;
    transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
  }

  a:hover:after {
    background: transparent;
    width: 110%;
    transition: 0s;
  }
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;
$font-3: "font-3", sans-serif;
$font-4: "font-4", sans-serif;
$font-5: "font-5", sans-serif;
$background-color: #f8f8f8;
$color-1: #000;
$color-2: #9d9d9d;
$color-3: #a4a4a4;
$color-4: #7a7a7a;
$color-5: #e8e8e8;
$color-6: #474747;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
  border: none;
  text-decoration: none;
  // overflow: hidden;
}
a {
  cursor: pointer;
}
a:visited {
  color: $color-1;
}
body {
  font-family: $font-1;
  height: 100vh;
  overflow-x: hidden;
}

.contact,
.works,
.about,
.home {
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
}

.load-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 10;
  pointer-events: none;
}
.load-screen {
  position: relative;
  padding-top: 0px;
  padding-left: 0px;
  padding-right: 0px;
  background-color: #000;
  width: 0%;
  height: 100%;
}

.Headd {
  opacity: 0;
  pointer-events: none;
}
